@import '../../settings';

// $borderradius, $minwidth, $padding, $fontsize, $fontweight, $lineheight, $letterspacing
@mixin mybutton($borderradius, $minwidth, $padding, $fontsize, $fontweight, $lineheight, $letterspacing) {
	.button {
		background: $blau;
		border-radius: rem-calc($borderradius);
		border: none;
		color: $white;
		margin-bottom: 0;
		min-width: rem-calc($minwidth);
		padding: rem-calc($padding);
		position: absolute;
		text-align: center;
		transform: translateX(-50%) translateY(-50%);
		white-space: nowrap;
		@include font-styling($fontsize, $fontweight, $lineheight, $letterspacing, none);

		.backtopage {
			display: inline-block;
			width: rem-calc(20px);
			height: 1.25rem;

			path {
				fill: white;
			}
		}

		&:hover, &:focus {
			background: $gelb;
			border: none;
			color: $blau;

			.backtopage path {
				fill: $blau;
			}
		}
	}
}