@import '../settings';

// $fontsize, $fontweight, $lineheight, $letterspacing, $uppercase
@mixin font-styling($fontsize, $fontweight, $lineheight, $letterspacing, $uppercase) {
	font-family: $body-font-family;
	font-weight: $fontweight;
	font-size: $fontsize;
	line-height: $lineheight;
	letter-spacing: $letterspacing;
	text-transform: $uppercase;
}

header {
	width: 100%;

	[class*="ce__header--"] {
		margin: 0;
	}
	.ce__header--left {
		text-align: left;
	}

	.ce__header--center {
		text-align: center;
	}

	.ce__header--right {
		text-align: right;
	}
}

.ce__header {
	color: $dunkelgrau;
	/*
	&::after {
		content: "grau";
	}
	*/
}

.ce__header--1 {
	color: $blau;
	/*
	&::after {
		content: "blau";
	}
	*/
}

h1 {
	@include font-styling(32px, 400, 52px, 0, uppercase);
	margin-bottom: rem-calc(40px);
	/*
	&::after {
		content: "h1";
		color: red;
		font-size: 1rem;
		padding-left: 1rem;
	}
	*/
}

h2 {
	@include font-styling(26px, 600, 48px, 0, uppercase);
	// margin-bottom: rem-calc(40px);
}

h3 {
	@include font-styling(26px, 600, 44px, 0.65px, uppercase);
	/*
	&::after {
		content: "h2";
		color: red;
		font-size: 1rem;
		padding-left: 1rem;
	}
	*/
}
h4 {
	/*
	&::after {
		content: "h4";
		color: red;
		font-size: 1rem;
		padding-left: 1rem;
	}
	*/
}

.lowercase {
	h3, h4 {
		text-transform: none;
	}
}