@import 'settings';
@import '_components/_font-styling';
@import '_components/_linking';
@import '_components/_image-descriptions';
@import 'buttons/_components/_buttons.scss';

@mixin news-img-wrap() {
	a {
		background: none;
		border: none;
		float: none;
		padding: 0;

		picture {
			display: block;
		}
	}
}

.main-area [class*="frame-type-news_"] {

	// $borderradius, $minwidth, $padding, $fontsize, $fontweight, $lineheight, $letterspacing
	@include mybutton(45px, 670px, 14px, 26px, 600, 48px, 0.65px);


	.news ul {
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		list-style: none;
		margin: 0 auto;

		li {
			display: flex;

			a {
				color: $blau;
				font-size: 16px;
				font-weight: 600;
			}

			a.active {
				border-bottom: 2px solid;
			}

			&::after {
				border-right: 1px solid;
				content: "";
				display: inline-block;
				height: 100%;
				margin: 0.4rem .25rem 0;
				max-height: 14px;
			}

			&:last-child::after {
				display: none;
			}
		}
	}

	.news {
		.article {
			&.cell {
				display: flex;
				// margin-bottom: 0;
				// padding: 0.9375rem 0;
				padding: 0;
			}
			&::after {
				display: none;
			}
		}

		.columns {
			height: 100%;
			padding: rem-calc(20px) rem-calc(20px) rem-calc(30px);

			.panel-body {
				display: flex;
				flex-direction: row;
				flex-wrap: wrap;
				height: 100%;

				.header {
					@include font-styling(22px, 600, 39px, 0.65px, uppercase);
					flex: 100% 0 0;
					order: 2;
					// border: 1px solid yellow;
				}

				.teaser-text {
					color: $blau;
					flex-wrap: wrap;
					order: 3;
					// border: 1px solid red;
				}

				@include read-more();

				.footer {
					@include font-styling(22px, 600, 33px, 0.55px, uppercase);
					border: none;
					color: $blau;
					display: flex;
					flex-direction: row;
					flex: 100% 0 0;
					flex-wrap: wrap;
					margin: 0;
					padding: 0;
					order: 1;
					// border: 1px solid blue;

					p {
						width: 100%;
					}
				}
			}
		}

		.template-projects {
			.header {
				order: unset;
				margin-bottom: rem-calc(35);

				h3 {
					width: 100%;
					border-bottom: 3px dotted $mittelgrau;

					a {
						border-bottom: 3px dotted $mittelgrau;
						display: block;

						&:hover, &:focus {

						}
					}
				}
			}

			.panel-body {
				padding-left: 10px;
				padding-right: 10px;

				.teaser-text {
					order: unset;
				}

				.news-img-wrap {
					margin-top: 0;
					// margin: 3px 8px;

					@include news-img-wrap();
				}

				.news-text-wrap {
					@include read-more();
				}
			}
		}
	}

	.background-color-2 {
		// background: $hellgrau;

		&:hover, &:focus {
			background: $gelb;
		}
	}

	&.frame-type-news_newsdetail {

		.news-single {
			.article {
				display: flex;
				flex-wrap: wrap;

				.header {
					order: 1;
					width: 100%;
				}

				.news-img-wrap {
					// margin-bottom: 2.8125rem;
					order: 2;
				}

				.news-text-wrap {
					order: 3;
				}

				.footer {
					display: none;
				}

				.news-contentElementRendering {
					order: 4;
				}
			}
		}

		.columns {
			height: auto;
		}

		.news [class*="frame-type-"] {
			padding: 1.25rem 0 1.875rem;
		}

		.news-img-wrap {
			display: block;
			float: none;
			margin: 0;
			width: 100%;

			@include news-img-wrap();
		}
	}
}
