@import '_font-styling';

@mixin linking() {
	a {
		display: block;
		border: none;
		border-radius: 25px;
		color: $white;
		background: $blau;
		line-height: normal;
		margin: 0 .5rem 0 0;
		padding: 0 30px;

		@include font-styling(22px, 600, 44px, 0, none);

		&::after {
			background: url('data:image/svg+xml,<svg id="intern" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 33.36 35.85"><defs><style>.cls-1{fill:%23fff;stroke-width:0px;}</style></defs><path class="cls-1" d="M33.36,5.4v25.05c0,2.98-2.42,5.4-5.4,5.4h-11.32c-.66,0-1.2-.54-1.2-1.2h0c0-.66.54-1.2,1.2-1.2h11.32c1.66,0,3-1.34,3-3V5.4c0-1.66-1.34-3-3-3h-11.32c-.66,0-1.2-.54-1.2-1.2h0c0-.66.54-1.2,1.2-1.2h11.32c2.98,0,5.4,2.42,5.4,5.4ZM18.91,17.63l-2.73-3.2c-.26-.3-.75-.12-.75.28v2H1.2c-.66,0-1.2.54-1.2,1.2s.54,1.2,1.2,1.2h14.22v2c0,.39.49.58.75.28l2.73-3.2c.14-.16.14-.39,0-.55Z"/></svg>') no-repeat;
			content: "";
			display: inline-block;
			margin-left: .5rem;
			width: 1.5rem;
			height: 1.75rem;

			float: right;
			margin-top: 8px;
		}

		&:hover,
		&:focus {
			color: $blau;
			background: $gelb;

			&::after {
				background: url('data:image/svg+xml,<svg id="intern" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 33.36 35.85"><defs><style>.cls-1{fill:$blau;stroke-width:0px;}</style></defs><path class="cls-1" d="M33.36,5.4v25.05c0,2.98-2.42,5.4-5.4,5.4h-11.32c-.66,0-1.2-.54-1.2-1.2h0c0-.66.54-1.2,1.2-1.2h11.32c1.66,0,3-1.34,3-3V5.4c0-1.66-1.34-3-3-3h-11.32c-.66,0-1.2-.54-1.2-1.2h0c0-.66.54-1.2,1.2-1.2h11.32c2.98,0,5.4,2.42,5.4,5.4ZM18.91,17.63l-2.73-3.2c-.26-.3-.75-.12-.75.28v2H1.2c-.66,0-1.2.54-1.2,1.2s.54,1.2,1.2,1.2h14.22v2c0,.39.49.58.75.28l2.73-3.2c.14-.16.14-.39,0-.55Z"/></svg>') no-repeat;
			}
		}
	}

	a[href^="http"],
	a[href^="https"] {
		display: inline-block;

		&::after {
			color: $white;
			background: url('data:image/svg+xml,<svg id="extern" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 35.85 35.85"><defs><style>.cls-1{fill:%23fff;stroke-width:0px;}</style></defs><g id="Ebene_1-2"><path class="cls-1" d="M34.65,14.98h0c.66,0,1.2.54,1.2,1.2v14.27c0,2.98-2.42,5.4-5.4,5.4H5.4c-2.98,0-5.4-2.42-5.4-5.4V5.4C0,2.42,2.42,0,5.4,0h14.27c.66,0,1.2.54,1.2,1.2h0c0,.66-.54,1.2-1.2,1.2H5.4c-1.66,0-3,1.34-3,3v25.05c0,1.66,1.34,3,3,3h25.05c1.66,0,3-1.34,3-3v-14.27c0-.66.54-1.2,1.2-1.2ZM35.52.72c-.02-.21-.18-.37-.39-.39L30.93,0c-.39-.03-.61.45-.33.72l1.41,1.41-10.06,10.06c-.47.47-.47,1.23,0,1.7.23.23.54.35.85.35s.61-.12.85-.35l10.06-10.06,1.41,1.41c.28.28.76.06.72-.33l-.33-4.19Z"/></g></svg>') no-repeat right;
		}

		&:hover::after, &:focus::after {
			background: url('data:image/svg+xml,<svg id="extern" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 35.85 35.85"><defs><style>.cls-1{fill:$blau;stroke-width:0px;}</style></defs><g id="Ebene_1-2"><path class="cls-1" d="M34.65,14.98h0c.66,0,1.2.54,1.2,1.2v14.27c0,2.98-2.42,5.4-5.4,5.4H5.4c-2.98,0-5.4-2.42-5.4-5.4V5.4C0,2.42,2.42,0,5.4,0h14.27c.66,0,1.2.54,1.2,1.2h0c0,.66-.54,1.2-1.2,1.2H5.4c-1.66,0-3,1.34-3,3v25.05c0,1.66,1.34,3,3,3h25.05c1.66,0,3-1.34,3-3v-14.27c0-.66.54-1.2,1.2-1.2ZM35.52.72c-.02-.21-.18-.37-.39-.39L30.93,0c-.39-.03-.61.45-.33.72l1.41,1.41-10.06,10.06c-.47.47-.47,1.23,0,1.7.23.23.54.35.85.35s.61-.12.85-.35l10.06-10.06,1.41,1.41c.28.28.76.06.72-.33l-.33-4.19Z"/></g></svg>') no-repeat right;
		}
	}

	a[href^="mailto"] {
		// background: url('data:image/svg+xml,<svg id="extern" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 35.85 35.85"><path class="cls-1" d="M34.65,14.98h0c.66,0,1.2.54,1.2,1.2v14.27c0,2.98-2.42,5.4-5.4,5.4H5.4c-2.98,0-5.4-2.42-5.4-5.4V5.4C0,2.42,2.42,0,5.4,0h14.27c.66,0,1.2.54,1.2,1.2h0c0,.66-.54,1.2-1.2,1.2H5.4c-1.66,0-3,1.34-3,3v25.05c0,1.66,1.34,3,3,3h25.05c1.66,0,3-1.34,3-3v-14.27c0-.66.54-1.2,1.2-1.2ZM35.52.72c-.02-.21-.18-.37-.39-.39L30.93,0c-.39-.03-.61.45-.33.72l1.41,1.41-10.06,10.06c-.47.47-.47,1.23,0,1.7.23.23.54.35.85.35s.61-.12.85-.35l10.06-10.06,1.41,1.41c.28.28.76.06.72-.33l-.33-4.19Z"/></svg>') no-repeat right; no-repeat right;
	}

	a[href^="mailto"]:hover,
	a[href^="mailto"]:focus {
	}

	a[href*="fileadmin/user_upload"] {
		display: inline-block;
		margin: 0 auto;

		&::after {
			background: url('data:image/svg+xml,<svg id="download" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 35.85 25.36"><defs><style>.cls-1{fill:%23fff;stroke-width:0px;}</style></defs><path class="cls-1" d="M33.9,19.51h1.49c.25,0,.45.2.45.45h0c0,2.98-2.42,5.4-5.4,5.4H5.4c-2.98,0-5.4-2.42-5.4-5.4h0c0-.25.2-.45.45-.45h1.49c.25,0,.45.2.45.45h0c0,1.66,1.34,3,3,3h25.05c1.66,0,3-1.34,3-3h0c0-.25.2-.45.45-.45ZM21.12,15.42h-2V1.2c0-.66-.54-1.2-1.2-1.2s-1.2.54-1.2,1.2v14.22h-2c-.39,0-.58.49-.28.75l3.2,2.73c.16.14.39.14.55,0l3.2-2.73c.3-.26.12-.75-.28-.75Z"/></svg>') no-repeat right;
		}
	}

	a.read-more-link {
		background: none;
		margin-right: 0;
		padding-right: 0;

		&:focus, &:hover {

		}

		&::after {
			display: none;
		}
	}
}

@mixin focusing() {
	:focus-visible {
		box-shadow: 0 0 5px $blau;
		outline: none;
	}
}

@mixin read-more() {
	.read-more {
		align-items: flex-end;
		display: flex;
		flex-direction: row;
		justify-content: flex-end;

		.description {
			display: flex;
			flex-direction: column;
		}

		.read-more-link {
			@include font-styling(inherit, 600, 22px, 0, none);
			align-items: flex-end;
			border-bottom: 2px solid transparent;
			border-radius: 0;
			color: $blau;
			display: inline;
			flex-direction: column;
			justify-content: flex-end;
			padding-left: 0;
			width: auto;

			&:hover, &:focus {
				border-bottom: 2px solid $blau;
			}
		}

		&.grid-x.grid-margin-x {
			.read-more-link {
				margin: 0;
				// width: 100%;
			}
		}
	}
}
